#c {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: block;
    z-index: -1;
}

.viewContainer {
    float: left;
    width: 100%;
    margin: 0;
    padding: 0;
}

.view {
    float: left;
    width: calc(100% - 20px);
    height: 200px;
    margin: 10px;
    padding: 0;
}

#viewBuilder {
    height: 420px;
    width: calc(60% - 20px);
}

#viewA {
    width: calc(40% - 20px);
}

#viewB {
    width: calc(40% - 20px);
}

#controls {
    float: left;
    width: calc(100% - 20px);
    margin: 10px;
}

#controls .button {
    font-size: small;
    width: 150px;
    height: 50px;
    white-space: normal;
    margin-right: 10px;
}
